import React from 'react';

function VerifyAccount(props) {
    return (
        <div className="page-single">
        <div className="container">
          <div className="row">
            <div className="col col-login mx-auto">
              <div className="text-center mb-6">
              <a className="navbar-brand text-brand" href="/">Greybox.<span className="color-b" style={{fontSize: 'medium'}}>Space</span></a>
              </div>
              <section className="intro-single" style={{"padding":0}}>
            <div className="container">
                <div className="row">
                    <div className="col-md-12 col-lg-8">
                        <div className="title-single-box">
                            <h1 className="title-single">Email Verification Needed!</h1>
                            <span className="color-text-a">Please do verify your account, check your inbox.</span>
                        </div>
                    </div>
                </div>
            </div>
        </section>
              <div className="text-center text-muted">
                You have not got the mail? <a href="/resend-verification-mail">Resend Verification Mail</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
}

export default VerifyAccount;