import React, { Fragment, useEffect, useState } from 'react';
import { ImImages } from 'react-icons/im';
import { FiSettings,FiPlusCircle, FiEdit } from 'react-icons/fi';
import { GrView, GrLocation, GrAddCircle } from 'react-icons/gr';
import { BiLike } from 'react-icons/bi';
import Pagination from '../common/Pagination';

import { deleteProperty, getMyProperties, markPropertyAsDeactivated,markPropertyAsActivate } from './../services/propertyService';
import { paginate } from './../utils/paginate';
import { Link } from 'react-router-dom';
import { BsPlusCircle } from 'react-icons/bs';
import { property } from 'lodash';


function PropertyList(props) {
  const [myProperties, setMyProperties] = useState([]);
  const [itemsCount, setItemCount] = useState(0);
  const [pageSize, setPageSize] = useState(5);
  const [currentPage, setCurrentPage] = useState(1);
  const a ={};
  

  const handlePageChange = (page) => {
    setCurrentPage(page);
    let properties = getMyProperties(); 
    let listProperty = paginate(properties, page, pageSize);
    setMyProperties(listProperty);
  };

  const handleDeactivateProperty = (publicId) => {
    let properties = markPropertyAsDeactivated(publicId);
    handlePageChange(currentPage);
    
    // setMyProperties(properties);
  }

  const handleActivateProperty = (publicId) => {
    let properties = markPropertyAsActivate(publicId);
    // setMyProperties(properties);
    handlePageChange(currentPage);

  }

  const handleDeleteProperty = (publicId) => {
    let properties = deleteProperty(publicId);
    handlePageChange(currentPage);
    // setMyProperties(properties);
    // setItemCount(properties.length);
  }

  const loadProperties= () => {
    let properties = getMyProperties();
    setItemCount(properties.length);
    let listProperty = paginate(properties, currentPage, pageSize);
    setMyProperties(listProperty);
  }

  useEffect(() => {
    loadProperties();
        return () => {
      console.log('Cleanup');
    }
  },[]);
  //// issue above 



  return (
    <Fragment>
      <section className="intro-single">
        <div className="container">
          <div className="row">
            <div className="col-md-12 col-lg-8">
              <div className="title-single-box">
                <h1 className="title-single">Properties</h1>
                <span className="color-text-a">List of properties posted by you</span>
              </div>
            </div>
            <div className="col-md-12 col-lg-4">

              <nav aria-label="breadcrumb" className="breadcrumb-box d-flex justify-content-lg-end">
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <a href="#">Home</a>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    Properties
                    </li>
                </ol>
              </nav>
              <div className="d-flex justify-content-lg-end">
                                <Link className="btn btn-lg btn-success" to="/add-property"><BsPlusCircle/> List Property</Link>
                            </div>

            </div>
          </div>
        </div>
      </section>


      {
        itemsCount !== 0 ?
          <div className="row row-cards row-deck">
            <div className="col-12">
              <div className="card">
                <div className="table-responsive p-4">
                  <table id="propertyTable" className="table table-hover table-outline table-vcenter text-nowrap card-table">
                    <thead>
                      <tr>
                        <th className="text-center w-1"><ImImages></ImImages>&nbsp;Images</th>
                        <th>Property Title</th>
                        <th className="text-center">Status</th>
                        <th className="text-center"><GrLocation></GrLocation>&nbsp;Location</th>
                        <th><GrView></GrView>&nbsp;Views&nbsp;/&nbsp;<BiLike></BiLike>&nbsp;Likes</th>
                        <th className="text-center">Posted On</th>
                        <th className="text-center"><FiSettings></FiSettings>&nbsp;Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        myProperties.map((pro,i) => {
                          return <tr key={'property-'+i}>
                            <td className="text-center">
                              <div>
                                <img height="100" width="150" src={pro.images[0]} />
                              </div>
                            </td>
                            <td>
                              <Link to={`/property/${pro.publicId}`}>Elizabeth Martin</Link>
                              <div className="small text-muted">
                                Size: {pro.area.value}{pro.area.symbol}<sup>2</sup>
                              </div>
                              <div className="small text-muted">
                                Listed For : {pro.status}
                              </div>
                              <div className="small text-muted">
                                Type: {pro.propertyCategory.name}
                              </div>
                            </td>
                            <td className="text-center">
                              <span className={pro.postStatus.isActive ? "badge badge-success" : "badge badge-danger"}>{pro.postStatus.isActive ? "Active" : "Deactive"}</span>
                            </td>
                            <td className="text-center">
                              {pro.address.city}
                            </td>
                            <td className="text-center">
                              <div ><GrView></GrView>&nbsp;200</div>
                              <div><BiLike></BiLike>&nbsp;50</div>
                            </td>
                            <td className="text-center">
                              <div className="mx-auto chart-circle chart-circle-xs" data-value="0.42" data-thickness={3} data-color="blue">
                                <div className="chart-circle-value">{pro.timestamp}</div>
                              </div>
                            </td>
                            <td className="text-center">
                              <div className="btn-group btn-group-sm" role="group" aria-label="Button group with nested dropdown">
                                <button type="button" className="btn btn-secondary">Edit</button>
                                <div className="btn-group btn-group-sm" role="group">
                                  <button id="btnGroupDrop1" type="button" className="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                  </button>
                                  <div className="dropdown-menu" aria-labelledby="btnGroupDrop1">
                                    {
                                      !pro.postStatus.isActive ?
                                        <a className="dropdown-item" onClick={() => { handleActivateProperty(pro.publicId) }}>Activate</a>
                                        :
                                        <a className="dropdown-item" onClick={() => { handleDeactivateProperty(pro.publicId) }}>Deactivate</a>
                                    }
                                    <a className="dropdown-item" onClick={() => { handleDeleteProperty(pro.publicId) }}>Delete</a>
                                  </div>
                                </div>
                              </div>
                            </td>
                          </tr>
                        })
                      }

                    </tbody>
                  </table>
                  <Pagination itemsCount={itemsCount} currentPage={currentPage} pageSize={pageSize} onPageChange={handlePageChange} />
                </div>
              </div>
            </div>
          </div>
          :
          <Fragment>
            <div className="container">
              <div className="row">
                <div className="col-md-12 col-lg-8">
                  <div className="title-single-box">
                    <h1 className="title-single">No Property Listed</h1>
                    <span className="color-text-a">List your property here</span>
                  </div>
                </div>
              </div>
            </div>
          </Fragment>
      }
    </Fragment>

  );
}

export default PropertyList;