import {apiEndpoint} from './../config.json';
import httpService from './httpService';
import { addNewProperty } from './propertyService';

export const getCountries = async ()=>{
    try{
        const result = await httpService.get(`${apiEndpoint}location/country`);
        return result.data.success == true?result.data.data:[] ;
    }catch(err){
        console.log(err);
        return [];
    }
}

export const getStates = async (country) =>{
    try{
        const result = await httpService.get(`${apiEndpoint}location/state/${country.isoCode}`);
        return result.data.success == true?result.data.data:[] ;
    }catch(err){
        console.log(err);
        return [];
    }
}

export const getCities = async (state,countryCode)=>{
    try{
        const result = await httpService.get(`${apiEndpoint}location/city/${countryCode}/${state.isoCode}`);
        return result.data.success == true?result.data.data:[] ;
    }catch(err){
        console.log(err);
        return [];
    }
}

export const getCategories = async ()=>{
    try{
        const result = await httpService.get(`${apiEndpoint}property/categories`);
        console.log(result.data)
        return result.data.success == true?result.data.data:[] ;
    }catch(err){
        console.log(err);
        return [];
    }
}

export const postProperty = async (property)=>{
    try{
        const result = await httpService.post(`${apiEndpoint}property/`,property);
        console.log(result.data)
        if(result.data.success == true){
            addNewProperty(result.data.data);
            return result.data.data ;
        }
        return {};
    }catch(err){
        console.log(err);
        return {};
    }
}


