
let properties = [];

export const getMyProperties = ()=>{
    return properties;
}

export const markPropertyAsDeactivated=(publicId)=>{
    properties = properties.map(pro=>{
        if(pro.publicId == publicId)
        pro.postStatus.isActive = false;
        return pro;
    })
    return properties;
}

export const markPropertyAsActivate = (publicId)=>{
    properties = properties.map(pro=>{
        if(pro.publicId == publicId)
        pro.postStatus.isActive = true;
        return pro;
    })
    return properties;
}

export const deleteProperty = (publicId)=>{
    properties = properties.filter(pro=>{
        if(pro.publicId !== publicId)
        return pro;
    })
    return properties;
}

export const getPropertyStats = ()=>{
    return properties.length;
}

export const getPropertyByPublicId = (publicId)=>{
    let property = properties.filter(pro=>pro.publicId===publicId);
    return property;
}

export const addNewProperty = (property)=>{
    properties.unshift(property);
    return properties;
};