import { Fragment, useEffect, useState } from 'react';
import './App.css';
import jwtDecode from 'jwt-decode';
import Navbar from './common/Navbar';
import Login from './components/Login';
import Register from './components/Register';
import Dashboard from './components/Dashboard';
import PropertyList from './components/PropertyList';
import { Redirect, Route, Switch } from 'react-router';
import NotFound from './common/NotFound';
import ComingSoon from './common/ComingSoon';
import PropertyDetail from './components/PropertyDetail';
import AddProperty from './components/AddProperty';
import Footer from './common/Footer';
import VerifyAccount from './common/VerifyAccount';


// import mapboxgl from 'mapbox-gl/dist/mapbox-gl-csp';
// import MapboxWorker from 'worker-loader!mapbox-gl/dist/mapbox-gl-csp-worker';

// mapboxgl.workerClass = MapboxWorker;
// mapboxgl.accessToken = 'pk.eyJ1IjoiZ3JleWJveCIsImEiOiJja21qaTl1MDgwcjh5MnhtcWkyYWZ0ZWY1In0.a6DyfgiOIKsV3vY3cvwFNA';


function App() {
  const [user, setUser] = useState(null);
  const [isLoggedIn, setIsLoggedIn] = useState(false);


  useEffect(() => {
    // effect
    const jwt = localStorage.getItem('token');
    console.log(jwt == 'undefined', jwt == undefined);
    if (jwt !== 'undefined' && jwt !== undefined && jwt !== null) {
      const user = jwtDecode(jwt);
      setUser(user);
      setIsLoggedIn(true);
    }else{
      setUser(null);
      setIsLoggedIn(false);
    }
    return () => {
      // cleanup
    };
  },[])

  return (
    <Fragment>
      {
        !isLoggedIn &&
        <Fragment>
          <div className="container">
            <Switch>
              <Route path="/verify" exact render={(props) => <VerifyAccount {...props} />} />
              <Route path="/signup" exact render={(props) => <Register isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn} {...props} />} />
              <Route path="/login" exact render={(props) => <Login isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn} setUser={setUser} {...props} />} />
              <Redirect to="/login"></Redirect>
            </Switch>
          </div>
        </Fragment>
      }
      {
        isLoggedIn &&
        <Fragment>
          <Route path="/" render={(props) => <Navbar  user={user} isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn}  {...props} setUser={setUser}></Navbar>} />
          
          <div className="container">
            <Switch>
              <Route path="/property/:publicId" render={(props) => <PropertyDetail {...props} />} />
              <Route path="/connections" render={(props) => <ComingSoon sectionName='Connections' {...props} />} />
              <Route path="/tenants" render={(props) => <ComingSoon sectionName='Tenants' {...props} />} />
              <Route path="/property-list" component={PropertyList} />
              <Route path="/add-property" component={AddProperty} />
              <Route path="/not-found" exact component={NotFound} />
              <Route path="/" exact component={Dashboard} />
              <Redirect from='/login' to="/"></Redirect>
              <Redirect to="/not-found"></Redirect>
            </Switch>
          </div>
          <Footer />
        </Fragment>
      }
    </Fragment>
  );
}

export default App;
