import React from 'react';
import {NavLink} from 'react-router-dom';
import { GoSignOut } from 'react-icons/go';
import {FiSettings} from 'react-icons/fi';
import {BsShieldLock} from 'react-icons/bs';
import {RiAccountBoxLine } from 'react-icons/ri';

function Navbar(props) {
const handleLogout=()=>{
  // history.push('/')
  props.setIsLoggedIn(false);
  props.setUser(null);
  localStorage.removeItem('token');
  props.history.push('/login');
}

    return (
        <nav className="navbar navbar-default navbar-trans navbar-expand-lg fixed-top">
  <div className="container">
    <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#navbarDefault" aria-controls="navbarDefault" aria-expanded="false" aria-label="Toggle navigation">
      <span />
      <span />
      <span />
    </button>
    <a className="navbar-brand text-brand" href="/">Greybox.<span className="color-b" style={{fontSize: 'medium'}}>Space</span></a>

    <div className="navbar-collapse collapse justify-content-center" id="navbarDefault">
      <ul className="navbar-nav">
        <li className="nav-item">
          <NavLink className="nav-link" exact to="/">Dashboard</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/property-list">Properties</NavLink>
        </li>
        {/* <li className="nav-item">
          <NavLink className="nav-link" to="/connections">Connections</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/tenants">Tenants</NavLink>
        </li> */}
      </ul>
    </div>
    <div className="dropdown">
          <a className="nav-link dropdown-toggle d-none d-sm-block" id="navbarDropdown" role="button" data-toggle="dropdown" data-bs-display="static" aria-haspopup="true" aria-expanded="false">
          <img src={props.user && props.user.profilePic ||"https://res-1.cloudinary.com/crunchbase-production/image/upload/c_thumb,h_256,w_256,f_auto,g_faces,z_0.7,q_auto:eco/v1443584492/vosgqpvicpjgknz2rn2l.png"} alt="Avatar" className="avatar"/>
          &nbsp;{props.user && props.user.name||'Agent'}
          </a>
          <a className="nav-link dropdown-toggle d-block d-sm-none" id="navbarDropdown" role="button" data-toggle="dropdown" data-bs-display="static" aria-haspopup="true" aria-expanded="false">
          <img src={ props.user && props.user.profilePic ||"https://res-1.cloudinary.com/crunchbase-production/image/upload/c_thumb,h_256,w_256,f_auto,g_faces,z_0.7,q_auto:eco/v1443584492/vosgqpvicpjgknz2rn2l.png"}  alt="Avatar" className="avatar"/>
          </a>
          <div className="dropdown-menu dropdown-menu-right" aria-labelledby="navbarDropdown">
            <NavLink className="dropdown-item" to="/my-account"><RiAccountBoxLine></RiAccountBoxLine>&nbsp;&nbsp;Account</NavLink>
            <NavLink className="dropdown-item" to="/privacy"><BsShieldLock/>&nbsp;&nbsp;Privacy</NavLink>
            <NavLink className="dropdown-item" to="/settings"><FiSettings/>&nbsp;&nbsp;Settings</NavLink>
            <hr></hr>
            <a className="dropdown-item" onClick={handleLogout} ><GoSignOut></GoSignOut>&nbsp;&nbsp;Logout</a>
          </div>
        </div>
    </div>
</nav>

    );
}

export default Navbar;