import { apiEndpoint } from './../config.json';
import React, { Fragment, useEffect, useState } from 'react';
import { Col, Form, InputGroup, Button, Row, Container, Image, FormControl } from 'react-bootstrap';
import { FiTrash2 } from 'react-icons/fi';
import { ImCross } from 'react-icons/im';
import fileUploadService from './../services/fileUploadService';
import httpService from './../services/httpService';
import { getCountries, getStates, getCities, getCategories, postProperty } from './../services/apiService';
import Map from './../common/Map';



function FormExample(props) {
    const [validated, setValidated] = useState(false);
    const [property, setProperty] = useState({
        title: '',
        address: {
            addressLine1: '',
            addressLine2: '',
            city: '',
            state: '',
            country: '',
            pin: ''
        },
        images: [],
        cost: {
            currency: '',
            currencyCode: 'INR',
            value: 0,
        },
        propertyCategory: '',
        status: '',
        area: {
            unit: '',//'meter-square',
            symbol: '',
            value: 0
        },
        // quickSummary: {
        //     front: 20,
        //     depth: 50,
        //     beds: 5,
        //     baths: 5,
        //     garage: 5,
        //     facing: 'East'
        // },
        amenities: [],
        // videoURL: '',
        floorPlan: '',
        location: {
            lat: '',
            lng: ''
        },
        postStatus: {
            isActive: true,
        }
    });

    // const [amenities,setAmenities] = useState([{_id:'sadsda',name:'Near School'},{_id:'sadsda',name:'Near Hospital'}]);
    const [countryCode, setCountryCode] = useState('');
    const [propertyCategory, setPropertyCategory] = useState([]);

    const [countries, setCountries] = useState([]);
    const [states, setStates] = useState([]);
    const [cities, setCities] = useState([]);

    const handleSubmit = async (event) => {
        const form = event.currentTarget;
        
        
        setValidated(true);
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
            console.log('CANNOT SUBMITTED');
            return;
        }
        console.log('CAN BE SUBMITTED');
        console.log(property);
        ///API CALL HERE
        event.preventDefault();
        const response  = await postProperty(property);
        if(response.success){
            // props.history.push('/property-list');
        }
    };

    const uploadImage = async (e) => {
        let newProperty = { ...property };
        
        const fd = new FormData();
        // Update the formData object
        fd.append(
            "image",
            e.target.files[0], e.target.files[0].name
        );
        const response = await fileUploadService.uploadImage(fd);
        // newProperty.images.push(URL.createObjectURL(e.target.files[0]));
        newProperty.images.push(response.data.data[0].imageUrl);
        setProperty(newProperty);
    }

    const uploadFloorPlanImage = async (e) => {
        let newProperty = { ...property };
        // newProperty.floorPlan=URL.createObjectURL(e.target.files[0]);
        const fd = new FormData();
        fd.append(
            "image",
            e.target.files[0], e.target.files[0].name
        );
        const response = await fileUploadService.uploadImage(fd);
        newProperty.floorPlan=response.data.data[0].imageUrl;

        setProperty(newProperty);
        console.log(response);

        // Update the formData object
      
    }

    const handleDeleteImage = (img) => {
        let newProperty = { ...property };
        newProperty.images = newProperty.images.filter(i => i !== img);
        setProperty(newProperty);
    }

    const handleDeleteFloorPlan =()=>{
        let newProperty = { ...property };
        newProperty.floorPlan = '';
        setProperty(newProperty);
    }

    const handleCountryChange = e => {
        const country = JSON.parse(e.target.value);
        let newProperty = { ...property };
        newProperty.address.country = country.name;
        newProperty.cost.currency = country.currency;
        loadStates(country);
        setProperty(newProperty);
    }

    const handleStateChange = e => {
        const state = e.target.value;
        let newProperty = { ...property };
        newProperty.address.state = state.name;
        loadCities(JSON.parse(state));
        setProperty(newProperty);
    }

    const loadCountries = async () => {
        const fetchedCountries = await getCountries();
        console.log(fetchedCountries);
        setCountries(fetchedCountries);
    }

    const loadStates = async (country) => {
        setCountryCode(country.isoCode);
        const fetchedStates = await getStates(country);
        console.log(fetchedStates);
        setStates(fetchedStates);
    }

    const loadCities = async (state) => {
        console.log(state, '<<<<');
        const fetchedCities = await getCities(state, countryCode);
        console.log(fetchedCities);
        setCities(fetchedCities);
    }

    const loadCategories = async ()=>{
        const categories = await getCategories();
        console.log(categories);
        setPropertyCategory(categories);
    }

    useEffect(() => {
        loadCountries();
        loadCategories();
        return () => {
            // cleanup
        };
    }, [])

    return (
        <Form noValidate validated={validated} onSubmit={handleSubmit}>
            <Form.Row>
                <Form.Group as={Col} md="12" controlId="validationCustom01">
                    <Form.Label>Proprty Post Title</Form.Label>
                    <Form.Control
                        required
                        onChange={e => {
                            let newProperty = { ...property };
                            newProperty.title = e.target.value;
                            setProperty(newProperty);
                        }}
                        type="text"
                        placeholder="Enter Title Here"
                    />
                </Form.Group>
                <Form.Group as={Col} md="4" controlId="validationCustom02">
                    <Form.Label>Address Line 1</Form.Label>
                    <InputGroup hasValidation>

                        <Form.Control
                            type="text"
                            name='addressLine1'
                            placeholder="Address Line 1"
                            aria-describedby="inputGroupPrepend"
                            required
                            onChange={e => {
                                let newProperty = { ...property };
                                newProperty.address.addressLine1 = e.target.value;
                                setProperty(newProperty);
                            }}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter address line 1.
          </Form.Control.Feedback>
                    </InputGroup>
                </Form.Group>
                <Form.Group as={Col} md="4" controlId="validationCustomUsername">
                    <Form.Label>Address Line 2</Form.Label>
                    <InputGroup hasValidation>

                        <Form.Control
                            type="text"
                            placeholder="Address Line 2"
                            aria-describedby="inputGroupPrepend"
                            required
                            onChange={e => {
                                let newProperty = { ...property };
                                newProperty.address.addressLine2 = e.target.value;
                                setProperty(newProperty);
                            }}
                        />
                        <Form.Control.Feedback type="invalid">
                            Please enter address line 2.
          </Form.Control.Feedback>
                    </InputGroup>
                </Form.Group>
                <Form.Group as={Col} md="4" controlId="validationCustom03">
                    <Form.Label>Country</Form.Label>
                    <Form.Control as="select" required onChange={handleCountryChange} custom defaultValue=''>
                        <option key={'Select Country'} value='' disabled>Select Country</option>
                        {countries.map((country, i) => {
                            return <option key={'country' + i} value={JSON.stringify(country)}>{country.name}</option>
                        })}
                    </Form.Control>
                    <Form.Control.Feedback type="invalid">
                        Please slect a valid country.
        </Form.Control.Feedback>
                </Form.Group>
                <Form.Group as={Col} md="3" controlId="validationCustom04">
                    <Form.Label>State</Form.Label>
                    <Form.Control as="select" required onChange={handleStateChange} defaultValue=''>
                        <option key={'state'} value='' disabled>Select State</option>

                        {states.map((state, i) => {
                            return <option key={'state' + i} value={JSON.stringify(state)}>{state.name}</option>
                        })}
                    </Form.Control>
                    <Form.Control.Feedback type="invalid">
                        Please slect a valid state.
        </Form.Control.Feedback>
                </Form.Group>
                <Form.Group as={Col} required md="3" controlId="validationCustom05">
                    <Form.Label>City</Form.Label>

                    <Form.Control as="select" required onChange={e => {
                        let newProperty = { ...property };
                        newProperty.address.city = e.target.value;
                        setProperty(newProperty);
                    }} custom defaultValue=''>
                        <option key={'Select City'} value='' disabled>Select City</option>

                        {cities.map((city, i) => {
                            return <option key={'city' + i} value={JSON.stringify(city)}>{city.name}</option>
                        })}

                    </Form.Control>
                    <Form.Control.Feedback type="invalid">
                        Please slect a valid city.
        </Form.Control.Feedback>
                </Form.Group>
                <Form.Group as={Col} md="4" controlId="validationCustom01">
                    <Form.Label>Pincode</Form.Label>
                    <Form.Control
                        required
                        onChange={e => {
                            let newProperty = { ...property };
                            newProperty.address.pin = e.target.value;
                            setProperty(newProperty);
                        }}
                        type="text"
                        placeholder="Enter Pincode Here"
                    />
                </Form.Group>
            </Form.Row>

            <Form.Label>{property.images.length ? 'Image Preview' : ''}</Form.Label>
            <Container>
                <Row>
                    {property.images.map((img, i) => {
                        return <Col xs={4} md={2} key={'image-' + i}>
                            <div style={{ position: "relative" }}>
                                <a onClick={() => { handleDeleteImage(img) }} className="close AClass">
                                    <FiTrash2 />
                                </a>
                                <Image className="w-100 mb-3 gallery-img" src={img} />
                            </div>
                        </Col>
                    })}
                </Row>
            </Container>
            <Form.Row>
                <Form.Group>
                    <Form.Label>Upload Images</Form.Label>
                    <Form.File id="formcheck-api-custom" required custom>
                        <Form.File.Input id="imageInput" required onChange={uploadImage} />
                        <Form.File.Label data-browse="Select File">
                            Images
      </Form.File.Label>
                        {/* <Form.Control.Feedback type="valid">You did it!</Form.Control.Feedback> */}
                    </Form.File>
                </Form.Group>

            </Form.Row>
            <Container>
                <Row>
                    { property.floorPlan && <Col xs={4} md={2} key={'fp-image-'}>
                            <div style={{ position: "relative" }}>
                                <a onClick={() => { handleDeleteFloorPlan() }} className="close AClass">
                                    <FiTrash2 />
                                </a>
                                <Image className="w-100 mb-3 gallery-img" src={property.floorPlan} />
                            </div>
                        </Col>
                    }
                </Row>
            </Container>
            <Form.Row>
                <Form.Group>
                    <Form.Label>Upload Floor Plan</Form.Label>
                    <Form.File id="formcheck-api-custom" required custom>
                        <Form.File.Input id="imageInputFloorPlan" required onChange={uploadFloorPlanImage} />
                        <Form.File.Label data-browse="Select File">
                        Floor Plan
                    </Form.File.Label>
                    </Form.File>
                </Form.Group>

            </Form.Row>
            <Form.Group as={Row}>
                <Form.Label as="legend" column sm={2}>
                    Listing Property for :
      </Form.Label>
                <Col sm={10}>
                    <Form.Check
                        required
                        type="radio"
                        label="Sale"
                        name="status-property"
                        id="formHorizontalRadios1"
                        value='Sale'
                        onChange={e => {
                            let newProperty = { ...property };
                            newProperty.status = 'Sale';
                            setProperty(newProperty);
                        }} />
                    <Form.Check
                        required
                        type="radio"
                        label="Rent"
                        name="status-property"
                        id="formHorizontalRadios2"
                        value='Rent'

                        onChange={e => {
                            let newProperty = { ...property };
                            newProperty.status = 'Rent';
                            setProperty(newProperty);
                        }} />
                    

                </Col>
            </Form.Group>

            <Form.Row>

            <Form.Group as={Col} md="4" controlId="validationCustom05">
                <Form.Label>Property Type</Form.Label>

                <Form.Control as="select" onChange={e => {
                    let newProperty = { ...property };
                    newProperty.propertyCategory = e.target.value;
                    setProperty(newProperty);
                }} required defaultValue='' custom>
                    <option key={'Select Type'} value='' disabled>Select Type</option>

                    {propertyCategory.map((type, i) => {
                        return <option key={'type' + i} value={type._id.toString()}>{type.name}</option>
                    })}

                </Form.Control>
                <Form.Control.Feedback type="invalid">
                    Please select a valid property type.
        </Form.Control.Feedback>
            </Form.Group>
            </Form.Row>


            <Form.Row>
                <Form.Group as={Col} md="4" controlId="validationRent">
                    <Form.Label>Expected {property.status === 'Sale' ?'Price' : 'Rent'}</Form.Label>
                    <InputGroup className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text id="basic-validationRent">{property.cost.currency}</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                            required
                            placeholder= {property.status === 'Sale' ?'Expected Price' : 'Expected Rent'}
                            aria-label="cost"
                            aria-describedby="basic-validationRent"
                            type="number"

                            onChange={e => {
                                let newProperty = { ...property };
                                newProperty.cost.value = e.target.value;
                                setProperty(newProperty);
                            }}
                        />
                    </InputGroup>
                </Form.Group>
            </Form.Row>

            <Form.Row>
                <Form.Group as={Col} md="4" controlId="validationCustom02">
                    <Form.Label>Area</Form.Label>
                    <InputGroup className="mb-3">
                        <InputGroup.Prepend>
                            <InputGroup.Text id="basic-validationCustom02">Sq. Ft.</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                            required
                            placeholder= 'Area'
                            aria-label="area"
                            aria-describedby="basic-validationCustom02"
                            type="number"

                            onChange={e => {
                                let newProperty = { ...property };
                                newProperty.area.value = e.target.value;
                                newProperty.area.unit= 'square-feet';
                                newProperty.area.symbol= 'ft'; 
                                newProperty.area.value = e.target.value;
                                setProperty(newProperty);
                            }}
                        />
                    </InputGroup>
                </Form.Group>
            </Form.Row>

            <Row>
                <Form.Group as={Col} md="12" controlId="amenities">
                    <Form.Label>Amenities</Form.Label>
                    <Form.Control
                        required
                        onChange={e => {
                            let newProperty = { ...property };
                            if(e.target.value.trim() !== '') 
                            newProperty.amenities = e.target.value.split(',');
                            setProperty(newProperty);
                        }}
                        type="text"
                        placeholder="Please fill amenities comma seprated"
                    />
                </Form.Group>
                {/* <p>
                Please fill amenities comma seprated
                </p> */}
                </Row>

                {/* <Map/> */}

            <Form.Group>
                <Form.Check
                    required
                    label="Agree to terms and conditions"
                    feedback="You must agree before submitting."
                />
            </Form.Group>
            <Button type="submit" className="btn btn-success">Add Property</Button>
        </Form>);
}






function AddProperty(props) {

    const [city, setCity] = React.useState('');
    const emailError = true;
    const handleChange = (event) => {
        setCity(event.target.value);
    };


    return (
        <Fragment>
            <section className="intro-single">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 col-lg-8">
                            <div className="title-single-box">
                                <h1 className="title-single">Add Property</h1>
                                <span className="color-text-a">Add you property to the list so that users can find them easily!</span>
                            </div>
                        </div>
                        <div className="col-md-12 col-lg-4">
                            <nav aria-label="breadcrumb" className="breadcrumb-box d-flex justify-content-lg-end">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item">
                                        <a href="index.html">Home</a>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Add Property
            </li>
                                </ol>
                            </nav>
                        </div>
                    </div>
                </div>
            </section>
            <FormExample  {...props}/>
        </Fragment >
    );
}

export default AddProperty;