import {apiEndpoint} from './../config.json';
import httpService from './httpService';
import { store } from 'react-notifications-component';


export const registerUser = async (user)=>{
    try{
        const result = await httpService.post(`${apiEndpoint}auth/signup`,user);
        if(!result.data.success)
        store.addNotification({
            title: "Invalid Request",
            message: result.data.phone ||result.data.email || result.data.message  ,
            type: "warning",
            insert: "top",
            container: "top-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
              duration: 3000,
              onScreen: true
            }
          });
          else
          store.addNotification({
            title: "Registration Success",
            message: result.data.phone ||result.data.email || result.data.message  ,
            type: "success",
            insert: "top",
            container: "top-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
              duration: 3000,
              onScreen: true
            }
          });
        return result.data ;
    }catch(err){
        console.log(err);
        store.addNotification({
            title: "Error!",
            message: "error",
            type: "danger",
            insert: "top",
            container: "top-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
              duration: 3000,
              onScreen: true
            }
          });
        return err;
    }
}

