import React, { Fragment, useEffect, useState } from 'react';
import { FiEdit } from 'react-icons/fi';
import SimpleImageSlider from "react-simple-image-slider";

import { getPropertyByPublicId } from '../services/propertyService';

function PropertyDetail({ match }) {

    const [images, setImages] = useState([]);
    const [property, setProperty] = useState(null);




    useEffect(() => {
        // effect
        //load property to display
        // console.log(match.params.publicId)
        const property = getPropertyByPublicId(match.params.publicId);
        setProperty(property[0]);
        // const imgs = [];
        // property[0].images.map(img => imgs.push({ url: img }));
        // setImages(imgs);
        return () => {
            // cleanup
        }
    })
    if (property === null) return <div className="container intro-single">
        <div className="row">
            <div className="col-md-12">
                <div className="error-template">
                    <h1>
                        Oops!</h1>
                    <h2>
                        Invalid Property</h2>
                    <div className="error-details">
                        Sorry, an error has occured, Requested property invalid or deleted!
                </div>
                </div>
            </div>
        </div>
    </div>
    return (
        <>
            {/*/ Intro Single star /*/}
            <section className="intro-single">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 col-lg-8">
                            <div className="title-single-box">
                                <h1 className="title-single">{property.address.addressLine1}</h1>
                                <span className="color-text-a">{property.address.city}, {property.address.state} {property.address.pin}</span>
                            </div>
                        </div>
                        <div className="col-md-12 col-lg-4">
                            <div className="d-flex justify-content-lg-end">
                                <button className="btn btn-lg btn-success"><FiEdit /> Edit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            {/* / Intro Single End /*/}
            {/*/ Property Single Star / */}
            <section className="property-single nav-arrow-b">
                <div className="container">
                    <div className="row">
                        <div className="col-sm-12">
                            <div id="property-single-carousel" className="owl-carousel owl-arrow gallery-property">
                                <div>
                                    {/* <SimpleImageSlider
                                        showNavs={true}
                                        showBullets={true}
                                        width={1080}
                                        height={504}
                                        images={images}
                                    /> */}
                                </div>
                            </div>
                            <div className="row justify-content-between">
                                <div className="col-md-5 col-lg-4">
                                    <div className="property-price d-flex justify-content-center foo">
                                        <div className="card-header-c d-flex">
                                            <div className="card-box-ico">
                                                <span className="ion-money">{property.cost.currency}</span>
                                            </div>
                                            <div className="card-title-c align-self-center">
                                                <h5 className="title-c">{property.cost.value}</h5>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="property-summary">
                                        <div className="row">
                                            <div className="col-sm-12">
                                                <div className="title-box-d section-t4">
                                                    <h3 className="title-d">Quick Summary</h3>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="summary-list">
                                            <ul className="list">
                                                <li className="d-flex justify-content-between">
                                                    <strong>Property ID:</strong>
                                                    <span>{property.publicId}</span>
                                                </li>
                                                <li className="d-flex justify-content-between">
                                                    <strong>Location:</strong>
                                                    <span>{property.address.city}, {property.address.state} {property.address.pin}</span>
                                                </li>
                                                <li className="d-flex justify-content-between">
                                                    <strong>Property Type:</strong>
                                                    <span>{property.propertyType}</span>
                                                </li>
                                                <li className="d-flex justify-content-between">
                                                    <strong>Status:</strong>
                                                    <span>{property.status}</span>
                                                </li>
                                                <li className="d-flex justify-content-between">
                                                    <strong>Area:</strong>
                                                    <span>{property.area.value}{property.area.symbol}
                                                        <sup>2</sup>
                                                    </span>
                                                </li>
                                                <li className="d-flex justify-content-between">
                                                    <strong>Beds:</strong>
                                                    <span>{property.quickSummary.beds}</span>
                                                </li>
                                                <li className="d-flex justify-content-between">
                                                    <strong>Baths:</strong>
                                                    <span>{property.quickSummary.baths}</span>
                                                </li>
                                                <li className="d-flex justify-content-between">
                                                    <strong>Garage:</strong>
                                                    <span>{property.quickSummary.garage}</span>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-7 col-lg-7 section-md-t3">
                                    <div className="row">
                                        <div className="col-sm-12">
                                            <div className="title-box-d">
                                                <h3 className="title-d">Property Description</h3>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="property-description">
                                        <p className="description color-text-a">
                                            Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia Curae; Donec velit
                                            neque, auctor sit amet
                                            aliquam vel, ullamcorper sit amet ligula. Cras ultricies ligula sed magna dictum porta.
                                            Curabitur aliquet quam id dui posuere blandit. Mauris blandit aliquet elit, eget tincidunt
                                            nibh pulvinar quam id dui posuere blandit.
                </p>
                                        <p className="description color-text-a no-margin">
                                            Curabitur arcu erat, accumsan id imperdiet et, porttitor at sem. Donec rutrum congue leo eget
                                            malesuada. Quisque velit nisi,
                                            pretium ut lacinia in, elementum id enim. Donec sollicitudin molestie malesuada.
                </p>
                                    </div>
                                    <div className="row section-t3">
                                        <div className="col-sm-12">
                                            <div className="title-box-d">
                                                <h3 className="title-d">Amenities</h3>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="amenities-list color-text-a">
                                        <ul className="list-a no-margin">
                                            {property.amenities.map((am,i) => {
                                                return <li key={'list-pd-'+i}>{am}</li>
                                            })}
                                        </ul>

                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-10 offset-md-1">
                            <ul className="nav nav-pills-a nav-pills mb-3 section-t3" id="pills-tab" role="tablist">
                                <li className="nav-item">
                                    <a className="nav-link active" id="pills-video-tab" data-toggle="pill" href="#pills-video" role="tab" aria-controls="pills-video" aria-selected="true">Video</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" id="pills-plans-tab" data-toggle="pill" href="#pills-plans" role="tab" aria-controls="pills-plans" aria-selected="false">Floor Plans</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" id="pills-map-tab" data-toggle="pill" href="#pills-map" role="tab" aria-controls="pills-map" aria-selected="false">Ubication</a>
                                </li>
                            </ul>
                            <div className="tab-content" id="pills-tabContent">
                                <div className="tab-pane fade show active" id="pills-video" role="tabpanel" aria-labelledby="pills-video-tab">
                                    <iframe src={property.videoURL} width="100%" height={460} frameBorder={0} webkitallowfullscreen='true' mozallowfullscreen='true' allowFullScreen />
                                </div>
                                <div className="tab-pane fade" id="pills-plans" role="tabpanel" aria-labelledby="pills-plans-tab">
                                    <img src={property.floorPlan} alt='' className="img-fluid" />
                                </div>
                                <div className="tab-pane fade" id="pills-map" role="tabpanel" aria-labelledby="pills-map-tab">
                                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3022.1422937950147!2d-73.98731968482413!3d40.75889497932681!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89c25855c6480299%3A0x55194ec5a1ae072e!2sTimes+Square!5e0!3m2!1ses-419!2sve!4v1510329142834" width="100%" height={460} frameBorder={0} style={{ border: 0 }} allowFullScreen />
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12">
                            <div className="row section-t3">
                                <div className="col-sm-12">
                                    <div className="title-box-d">
                                        <h3 className="title-d">Contact Agent</h3>
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6 col-lg-4">
                                    <img src="img/agent-4.jpg" alt='' className="img-fluid" />
                                </div>
                                <div className="col-md-6 col-lg-4">
                                    <div className="property-agent">
                                        <h4 className="title-agent">Anabella Geller</h4>
                                        <p className="color-text-a">
                                            Nulla porttitor accumsan tincidunt. Vestibulum ac diam sit amet quam vehicula elementum sed sit amet
                                            dui. Quisque velit nisi,
                                            pretium ut lacinia in, elementum id enim.
                </p>
                                        <ul className="list-unstyled">
                                            <li className="d-flex justify-content-between">
                                                <strong>Phone:</strong>
                                                <span className="color-text-a">(222) 4568932</span>
                                            </li>
                                            <li className="d-flex justify-content-between">
                                                <strong>Mobile:</strong>
                                                <span className="color-text-a">777 287 378 737</span>
                                            </li>
                                            <li className="d-flex justify-content-between">
                                                <strong>Email:</strong>
                                                <span className="color-text-a">annabella@example.com</span>
                                            </li>
                                            <li className="d-flex justify-content-between">
                                                <strong>Skype:</strong>
                                                <span className="color-text-a">Annabela.ge</span>
                                            </li>
                                        </ul>
                                        <div className="socials-a">
                                            <ul className="list-inline">
                                                <li className="list-inline-item">
                                                    <a href="#">
                                                        <i className="fa fa-facebook" aria-hidden="true" />
                                                    </a>
                                                </li>
                                                <li className="list-inline-item">
                                                    <a href="#">
                                                        <i className="fa fa-twitter" aria-hidden="true" />
                                                    </a>
                                                </li>
                                                <li className="list-inline-item">
                                                    <a href="#">
                                                        <i className="fa fa-instagram" aria-hidden="true" />
                                                    </a>
                                                </li>
                                                <li className="list-inline-item">
                                                    <a href="#">
                                                        <i className="fa fa-pinterest-p" aria-hidden="true" />
                                                    </a>
                                                </li>
                                                <li className="list-inline-item">
                                                    <a href="#">
                                                        <i className="fa fa-dribbble" aria-hidden="true" />
                                                    </a>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>


    );
}

export default PropertyDetail;