import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';


import { Col, Form, Button } from 'react-bootstrap';
import { loginUser } from '../services/authService';

function LoginForm(props) {
    const [validated, setValidated] = useState(false);
    const [userCred,setUserCred]=useState({
      email:'',
      password:''
    });


    // Keep track of input validation/errors
    const [error, setError] = useState({});


    const handleSubmit = async (event) => {
      const form = event.currentTarget;
      setValidated(true);
      if (form.checkValidity() === false) {
        event.preventDefault();
        event.stopPropagation();
        return;
      }
      event.preventDefault();
      let loginResponse = await loginUser(userCred);
      console.log(loginResponse);
      if(loginResponse.success){

        if(loginResponse.verified === false)
        return props.history.push('/verify')
        
        localStorage.setItem('token',loginResponse.data.token);
        props.setIsLoggedIn(true);
        props.history.push('/');
      }
      else{
        setError({email:true, password:true});
      }
    };

    function handleAuthenticationError(err) {
      console.error(err);
      const { status, message } = parseAuthenticationError(err);
      const errorType = message || status;
      switch (errorType) {
        case "invalid username":
          setError((prevErr) => ({ ...prevErr, email: "Invalid email address." }));
          break;
        case "invalid username/password":
        case "invalid password":
        case "401":
          setError((err) => ({ ...err, password: "Incorrect password." }));
          break;
        case "name already in use":
        case "409":
          setError((err) => ({ ...err, email: "Email is already registered." }));
          break;
        case "password must be between 6 and 128 characters":
        case "400":
          setError((err) => ({
            ...err,
            password: "Password must be between 6 and 128 characters.",
          }));
          break;
      }
    }
  
    return (
      <Form noValidate validated={validated} onSubmit={handleSubmit}>
            <div className="card-body p-6">     
        <Form.Row>

        
         
          <Form.Group as={Col} md="12" controlId="validationCustomEmail">
            <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                placeholder="Email"
                aria-describedby="inputGroupPrepend"
                className={error.email?'is-invalid':''}
                required
                onChange={e=>{
                  const user = {...userCred};
                  user.email = e.target.value;
                  setUserCred(user);
                }}
                isInvalid={error.email}
              />
              <Form.Control.Feedback type="invalid">
              {/* {error.email} */}
                Please a enter valid Email.
              </Form.Control.Feedback>
          </Form.Group>
        </Form.Row>
        <Form.Row>
          <Form.Group as={Col} md="12" controlId="validationCustom03">
            <Form.Label>Password  <Link to="/forgot-password" className="float-right small">I forgot password</Link ></Form.Label>
            <Form.Control type="password" placeholder="Password" onChange={e=>{
              const user = {...userCred};
              user.password = e.target.value;
              setUserCred(user);
            }} 
            isInvalid={error.password}
            required />
            <Form.Control.Feedback type="invalid">
            {/* {error.password} */}
              Invalid password.
            </Form.Control.Feedback>
          </Form.Group>
         
        </Form.Row>
       
        <div className="form-footer">
            <Button type="submit" className="btn btn-b-n btn-block">Login</Button>
        </div>
        </div>

      </Form>
    );
  }
  




