import React, { useEffect, useState } from 'react';
import { Col, Form, InputGroup,Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { registerUser } from './../services/authService';


function RegisterForm(props) {
    const [validated, setValidated] = useState(false);
    const [user,setUser]=useState({
        name:'',
        email:'',
        number:'',
        password:'',
        confirmPassword:''
    });
    
    const [errors,setErrors]= useState({});

    const handleSubmit = async (event) => {
      const form = event.currentTarget;
      setValidated(true);
      console.log(user);
      event.preventDefault();

      if (form.checkValidity() === false) {
        event.preventDefault();
        event.stopPropagation();
        return;
      }
  
      const newUser = await registerUser(user);
      console.log(newUser);
      if(newUser.success){
        return props.history.push('/verify')
      }
      
  
    };
  
    return (
      <Form noValidate validated={validated} onSubmit={handleSubmit}>
            <div className="card-body p-6">     
        <Form.Row>

          <Form.Group as={Col} md="12" controlId="validationCustom01">
            <Form.Label>Name</Form.Label>
            <Form.Control
              required
              type="text"
              placeholder="Name"
              onChange={e=>{
                const newUser = {...user};
                newUser.name = e.target.value;
                setUser(newUser);
              }}
              
              
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="12" controlId="validationCustomEmailPhone">
            <Form.Label>Phone Number</Form.Label>
              <Form.Control
                type="text"
                placeholder="Phone Number"
                aria-describedby="inputGroupPrepend"
                required
                minLength='4'
                maxLength='15'
                onChange={e=>{
                    const newUser = {...user};
                    newUser.number = e.target.value;
                    setUser(newUser);
                  }}
              />
              <Form.Control.Feedback type="invalid">
                Please a enter valid Phone Number.
              </Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="12" controlId="validationCustomEmail">
            <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                placeholder="Email"
                aria-describedby="inputGroupPrepend"
                required
                onChange={e=>{
                    const newUser = {...user};
                    newUser.email = e.target.value;
                    setUser(newUser);
                  }}
              />
              <Form.Control.Feedback type="invalid">
                Please a enter valid Email.
              </Form.Control.Feedback>
          </Form.Group>
        </Form.Row>
        <Form.Row>
          <Form.Group as={Col} md="12" controlId="validationCustom03">
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password" required 
              onChange={e=>{
                const newUser = {...user};
                newUser.password = e.target.value;
                setUser(newUser);
              }}
            />
            <Form.Control.Feedback type="invalid">
              Enter valid a password.
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="12" controlId="validationCustom03">
            <Form.Label>Confirm Password</Form.Label>
            <Form.Control type="password" placeholder="Confirm Password" isInvalid={user.password !== user.confirmPassword } required  
            onChange={e=>{
                const newUser = {...user};
                newUser.confirmPassword = e.target.value;
                setUser(newUser);
              }}/>
            <Form.Control.Feedback type="invalid">
              Passwords do not match.
            </Form.Control.Feedback>
          </Form.Group>
        </Form.Row>
        <Form.Group>
          <Form.Check
            required
            label="Agree to terms and conditions"
            feedback="You must agree before submitting."
          />
        </Form.Group>
        <div className="form-footer">
            <Button type="submit" className="btn btn-b-n btn-block">Create new account</Button>
        </div>
        </div>

      </Form>
    );
  }
  

