import React from 'react';
import _ from 'lodash';

function Pagination(props) {
    const {itemsCount,pageSize,currentPage} = props;
    const pagesCounts = Math.ceil(itemsCount/pageSize);
    if(pagesCounts === 1){
      return null;
    } 
    const pages = _.range(1,pagesCounts+1);
    return (
        <nav aria-label="Page navigation example">
        <ul className="pagination justify-content-end">
          {pages.map(page=>{
             return <li className={page === currentPage ? "page-item active":"page-item"} key={'table-pro-'+page}><a className="page-link" onClick={()=>props.onPageChange(page)}>{page}</a></li>
          })}
        </ul>
      </nav>
    );
}

export default Pagination;